.contact {
    background-color: #f4f4f4;
}

.contact h2 {
    margin-bottom: 50px;
}


.contact-icons {
    display: flex;
    justify-content: space-evenly;
    gap: 20px;
}

.contact-item {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.contact-item a {
    text-decoration: none;
}

.contact-label {
    margin-top: 10px;
    font-size: 16px;
    color: #333;
}

.contact-item svg {
    width: 70px;
    height: 70px;
}

@media (max-width: 768px) {
    .contact-item svg {
        width: 40px;
        height: 40px;
    }

    .contact-label {
        font-size: 12px;
    }
}